* {
    box-sizing: border-box;
}

.App {
    font-style: normal;
    overflow: hidden;
}

body {
    background: linear-gradient(to right, rgb(142, 158, 171, 0.3), rgb(238, 242, 243, 0.3));
}

.title {
    max-width: 1246px;
    width: 100%;
    font-family: 'Outfit', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 272px;
    color: rgba(106, 106, 106, 0.12);
    transform: translate3d(0, 0, 0);
}

.header_block {
    position: fixed;
    z-index: 10;
    width: 80%;
    left: 50%;
    top: 20px;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 64px;
    border-radius: 50px;
    background: linear-gradient(145deg, #cacaca, #f0f0f0);
    box-shadow:  8px 8px 16px #bebebe,
    -8px -8px 16px #ffffff;
}

.header_block .logo {
    font-family: 'Abril Fatface', cursive;
    font-weight: 700;
    font-size: 64px;
    line-height: 86px;
    color: #646464;
    margin: 0;
}

.header_block .links_block {
    display: flex;
}

.header_block .links_block .links {
    display: flex;
}

.header_block .links_block .links .link {
    list-style-type: none;
    cursor: pointer;
    margin-left: 56px;
}

.header_block .links_block .links .link .href {
    text-decoration: none;
    color: #454545;
    font-family: 'Outfit', sans-serif;
}

.person_block {
    position: relative;
    height: 800px;
    display: flex;
    padding: 0 10px;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.person_block .person_name {
    z-index: 1;
    position: absolute;
    pointer-events: none;
    font-family: 'Abril Fatface', cursive;
    font-weight: 400;
    font-size: 100px;
    line-height: 136px;
    color: #646464;
}

.person_block .person_profession {
    z-index: 1;
    position: absolute;
    pointer-events: none;
    margin-top: 250px;
    font-family: 'Outfit', sans-serif;
    font-weight: 200;
    font-size: 62px;
    line-height: 78px;
    color: #454545;
}

.about_block {
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 245px 0 92px 177px;
}

.about_block .about_placeholder {
    display: flex;
    justify-content: right;
    height: 343px;
}

.about_block .about_body {
    padding: 0 30px;
    display: flex;
}

.about_block .about_body .liner {
    max-width: 460px;
    width: 100%;
    height: 460px;
    display: flex;
    margin-top: 150px;
    justify-content: center;
    align-items: center;
    border: 11px solid #EDECE7;
    transform: rotate(30deg);
}

.about_block .about_body .liner .image_block {
    width: 100%;
    height: 100%;
    transform: rotate(-45deg);
    filter: drop-shadow(-11px 12px 19px rgba(0, 0, 0, 0.35));
