.aa {
    position: relative;
    padding: 0 30px;
    overflow: hidden;
}

.aa:before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    bottom: 2px;
    background: rgba(255, 255, 255, 0.05);
}

.aa span {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background: linear-gradient(to left, #eef2f3, #8e9eab);
    animation: animate3 3s linear infinite;
}

@keyframes animate3 {
    0% {
        transform: translateX(-100%);
        background: linear-gradient(to left, #eef2f3, #8e9eab);
    }
    2% {
        transform: translateX(-100%);
        background: linear-gradient(to right, #eef2f3, #8e9eab);
    }
    50% {
        transform: translateX(100%);
        background: linear-gradient(to right, #eef2f3, #8e9eab);
    }
    51% {
        transform: translateX(100%);
        background: linear-gradient(to left, #eef2f3, #8e9eab);
    }
    99% {
        transform: translateX(-100%);
        background: linear-gradient(to left, #eef2f3, #8e9eab);
    }
    100% {
        transform: translateX(-100%);
        background: linear-gradient(to right, #eef2f3, #8e9eab);
    }
}


.menu_block {
    width: 0;
    background-color: lightgrey;
    padding: 20px 20px;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    z-index: 10;
    transform: translateX(200px);
    transition: 0.3s transform ease-in-out, 0.3s width ease-in-out;
}

.show {
    width: 200px;
    transform: translateX(0);
}

.menu_block .closing {
    display: flex;
    justify-content: right;
}

.menu_block .closing .close_button {
    width: 30px;
    height: 30px;
    cursor: pointer;
    color: #646464;
    border-radius: 5px;
    border: 1px solid #646464;
}

.menu_block .menu_links_block .links .link {
    list-style-type: none;
    cursor: pointer;
    margin-bottom: 10px;
}

.menu_block .menu_links_block .links .link .href {
    text-decoration: none;
    color: #454545;
    font-size: 26px;
    font-family: 'Outfit', sans-serif;
}

.menu_icon {
    display: none;
}

.burger_menu {
    fill: #646464
}

.magnifying-glass {
    display: flex;
    width: 100%;
    height: 100%;
    opacity: 0;
}

.magnifying-glass__img {
    width: 100%;
}

.magnifying-glass__magnifier {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    overflow: hidden;
    width: 30vw;
    max-width: 20rem;
    height: 30vw;
    max-height: 20rem;
    border-radius: 80% 10% 45% 2%;
    background-color: transparent;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.25s ease;
    border: none;
}

.magnifying-glass__enlarged-image {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
}

.word-letter {
    display: inline-block;
    margin: 0 1px;
}

.slider_container {
    width: 270px;
    margin: 50px auto;
}

.slide_item {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}


